


























































































































































































	import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
	import cityData from '../../common/city.data.js';
	import uniIcon from '../../components/uni-icon.vue'
	
	export default {
		data() {
			return {
				avatar: '../../static/avatar/avatar_1.jpg',
				avatarName: '暂未设置',
				attrValue: '暂未设置',
				slTradeName: '暂未设置',
				pickerText: '暂未设置',
				index: 1,
				genders: ['男', '女'],
				
				pickerValueDefault: [0, 0],
				pickerValueArray:[],
				mulLinkageTwoPicker: cityData,
				themeColor: '#44b549',
				mode: '',
				deepLength: 1,
				
				showState: {
					top: false,
					middle: false,
					bottom: false,
					bottom1: false
				},
				showMask: false,
				activePop: 'middle',
				
				// 行业
				trades: [
					{
						id: 1,
						name: '学生'
					},
					{
						id: 2,
						name: '金融业'
					},
					{
						id: 3,
						name: '电商'
					},
					{
						id: 4,
						name: '医疗'
					},
					{
						id: 5,
						name: 'IT互联网'
					},
					{
						id: 6,
						name: '房地产'
					}
				],
				activeIndex: 9999,
				attrs: [
					{
						value: '0',
						name: '在校学生'
					},
					{
						value: '1',
						name: '宝妈宝爸',
						checked: 'true'
					},
					{
						value: '2',
						name: '上班族'
					},
					{
						value: '3',
						name: '职场白领'
					},
					{
						value: '4',
						name: '企业高管'
					},
					{
						value: '6',
						name: '自由职业'
					}
				],
			};
		},
		components: {
			mpvuePicker,
			uniIcon
		},
		methods: {
			genderChange: function(e){
				this.index = e.target.value
			},
			// 二级联动选择
			showMulLinkageTwoPicker() {
				this.pickerValueArray = this.mulLinkageTwoPicker
				this.mode = 'multiLinkageSelector'
				this.deepLength = 2
				this.pickerValueDefault = [0, 0]
				this.$refs.mpvuePicker.show()
			},
			onCancel(e) {
				console.log(e)
			},
			onConfirm(e) {
				let text = e.label;
				let index = text.indexOf('-')+1;
				text = text.substring(index);
				this.pickerText = text;
			},
			onTradeCancel(e) {
				this.hide()
			},
			onTradeTrue() {
				this.hide()
				this.slTradeName = this.tradeName
			},
			onAttrTrue() {
				this.hide();
				var attrValue = '';
				this.values.forEach(item => {
					console.log(item);
					attrValue = attrValue + ' ' + item
				})
				this.attrValue = attrValue;
			},
			getTradeName(index, name) {
				this.tradeName = name;
				this.activeIndex = index;	
			},
			show(e) {
				var pos = e.currentTarget.dataset.position;
				console.log(pos);
				switch (pos) {
					case 'top':
						this.activePop = 'top'
						break
					case 'bottom':
						this.activePop = 'bottom'
						break
					case 'bottom1':
						this.activePop = 'bottom1'
						break
					default:
						this.activePop = 'middle'
				}
				this.showMask = true
				this.showState[this.activePop] = true
			},
			hide() {
				this.showMask = false
				this.showState[this.activePop] = false
			},
			checkboxChange: function (e) {
				var values = e.detail.value;
				this.values = values;	
			},
			bindKeyInput: function(e){
				this.inputValue = e.target.value
			},
			// 设置昵称
			onSetavatarTrue: function(){
				this.hide();
				this.avatarName = this.inputValue
			},
			// 选择头像
			chooseImage: function(){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['camera', 'album'], //从相册选择
					success: function (res) {
						this.avatar = res.tempFilePaths;
						console.log(this.avatar);
						// console.log(JSON.stringify(res.tempFilePaths));
					}
				});
			},
			formSubmit: function (e) {
				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			},
			formReset: function () {
				console.log('form发生了reset事件')
			}
		},
		onBackPress() {
// 			if (this.$refs.mpvuePicker.showPicker) {
// 				this.$refs.mpvuePicker.pickerCancel();
// 				return true;
// 			}
// 			if (this.$refs.mpvueCityPicker.showPicker) {
// 				this.$refs.mpvueCityPicker.pickerCancel();
// 				return true;
// 			}
		},
		onUnload() {
// 			if (this.$refs.mpvuePicker.showPicker) {
// 				this.$refs.mpvuePicker.pickerCancel()
// 			}
// 			if (this.$refs.mpvueCityPicker.showPicker) {
// 				this.$refs.mpvueCityPicker.pickerCancel()
// 			}
		}
	}



